import React, {useState, useEffect, useContext } from "react"
//import { graphql } from "gatsby"
//import { navigate } from "@reach/router"
//import ThemeContext from "../utils/theme"
import { PageLayout } from "../components"
//import { SEO } from "../utils"
import { Container, Image  } from "react-bootstrap"
import Button from 'react-bootstrap/Button';
import Form from 'react-bootstrap/Form';
//import Spinner from 'react-bootstrap/Spinner'
//import { FontAwesomeIcon } from "@fortawesome/react-fontawesome"

import 'bootstrap/dist/css/bootstrap.css'; // or include from a CDN
import firebase from "../components/firebase"



export default () =>
{

  
  const [points, setP] = useState([]);

  const [cityForm,setCityForm] = useState("Field Required");
  const [stateForm,setStateForm] = useState("Field Required");
  const [disableSubmit,setSubmit] = useState(true);
  const [loading,setLoading] = useState("");
  
  
  const [houses,setHouses] = useState([]);
  const [filteredHouses,setFilteredHouses] = useState([]);
  const [testPrint,setTestPrint] = useState("");

  const handleSubmit = async (evt) => 
  {
    evt.preventDefault();
    const form = evt.target;
    const data = new FormData(form);

    let name = data.get("Name")

    let count=0
    let list = {}
    let temp
    while(count <= 20)
    {
      count++
      temp = String(data.get(`item${count}`))
      console.log(temp,temp.length)
      if(temp.length > 0)
      {
        let divider = temp.search("\\?")
        let ending = temp.substring((divider+1))
        const searchParams = new URLSearchParams(ending);
        let stateChosen = searchParams.get('state')
        let mlsChosen = searchParams.get('mls')
        if(stateChosen!=null && mlsChosen!=null)
        {
          let keyName = `${mlsChosen}_${stateChosen}`
          let houseData 
          await firebase.firestore().collection('States').doc(stateChosen).collection('buffer').doc(mlsChosen).get()
          .then((doc) => {
            if (doc.exists) 
            {
                console.log("Document data:", doc.data());
                houseData = doc.data()
            } 
            else 
            {
                // doc.data() will be undefined in this case
                console.log("No such document!");
            }
          }).catch((error) => {
              console.log("Error getting document:", error);
          });
          let houseCity = houseData.address.city
          let houseState = houseData.address.state
          let houseLine = houseData.address.line
          let houseZip = houseData.address.postal_code

          let addr = `${houseLine}, ${houseCity}, ${houseState}, ${houseZip}`

          let thumbNail = houseData.photos[0].href
          let trim = thumbNail.substring(0, (thumbNail.length-5))
          let betterThumbNail = trim.concat("od-w1024_h768_x2.webp")
          console.log(betterThumbNail)

          list[keyName] = betterThumbNail
        }
      }
    }


    //console.log("name is ",name,"| list is ", list)

    let entry = {}
    entry["Name"] = name
    entry["Rating"] = 5
    entry["List"] = list

    console.log( entry)

    firebase.firestore().collection('Categories').add(entry)

  }


  return(
    <>
    <PageLayout>
      <Container className=" align-items-center justify-content-center" >
        <h3>You can add a max of 20 homes per list</h3>
        <form onSubmit={handleSubmit} className="text-color" style={{display: `inline-block`}}>
          List Name: <Form.Control type='text' name='Name'/>
          <br/>
          URLs:
          <ol>
            <li><Form.Control type='text' name='item1'/></li>
            <li><Form.Control type='text' name='item2'/></li>
            <li><Form.Control type='text' name='item3'/></li>
            <li><Form.Control type='text' name='item4'/></li>
            <li><Form.Control type='text' name='item5'/></li>
            <li><Form.Control type='text' name='item6'/></li>
            <li><Form.Control type='text' name='item7'/></li>
            <li><Form.Control type='text' name='item8'/></li>
            <li><Form.Control type='text' name='item9'/></li>
            <li><Form.Control type='text' name='item10'/></li>
            <li><Form.Control type='text' name='item11'/></li>
            <li><Form.Control type='text' name='item12'/></li>
            <li><Form.Control type='text' name='item13'/></li>
            <li><Form.Control type='text' name='item14'/></li>
            <li><Form.Control type='text' name='item15'/></li>
            <li><Form.Control type='text' name='item16'/></li>
            <li><Form.Control type='text' name='item17'/></li>
            <li><Form.Control type='text' name='item18'/></li>
            <li><Form.Control type='text' name='item19'/></li>
            <li><Form.Control type='text' name='item20'/></li>
          </ol>
          <Button type="submit" value="Add"> Submit</Button>
        </form> 
        
      </Container>

    </PageLayout>
    </>
  )
}